body {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
}

.App {
    text-align: center;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.3);
    min-height: 100vh;
    background-color: #a499e6;
    margin: 0;
    padding: 0;
    font-size: 1.5em;
}

.masthead {
    background: #a499e6;
    color: #f5f5f5;
    width: 100%;
    margin: 0;
    padding: 10px 0;
}

.masthead a {
    color: #fff;
}

/* Hover effects for links */
.masthead a:hover, .landing-footer a:hover {
    text-decoration: underline;
}

.topic {
    margin-bottom: 15px;
    padding: 10px;
    background-color: #f5f5f5;
}

.topic h2 {
    color: #333;
}

.topic ul {
    display: block;
    margin: auto;
    padding: 0;
    width: 50%;
    list-style-type: none;
    text-align: left;
}

.topic li {
    color: #333; /* Set text color to dark grey */
    text-shadow: none;
    display: block;
    margin-bottom: 10px;
    padding-left: 20px; 
    position: relative;
    font-size: 0.85em;
}

.topic li:before {
    display:          inline-block;
    vertical-align:   middle;
    width:            8px;
    height:           8px;
    background-color: #000000;
    margin-right:     12px;
    content:          ' '
}

